@import "../../global.scss";

.menu {
  width: 33.3333vw;
  height: 100vh;
  background-color: white;
  position: fixed;
  top: 0;
  right: -33.3333vw;
  z-index: 6;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease;

  &.active {
    right: 0;
    color: white;
    background-color: $mainColor;
  }
  ul {
    // margin: 10px;
    // padding: 10px;
    list-style: none;
    font-size: clamp(3.3333vw, 4vh, 3.3333vw);
    color: white;

    li {
      width: 20vw;
      margin-bottom: 3.5vh;

      a {
        font-size: inherit;
        color: inherit;
        text-decoration: none;
      }

      &:hover {
        font-weight: 500;
      }
    }
  }
}
